* {
    scroll-behavior: inherit;
}

.portfolio {
    background-color:white;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1 {
        font-size: 45px;
        margin-bottom: 50px;
    }

    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;

        li {
            font-size: 30px;
            margin-right: 20px;
            padding: 7px;

            &.active {
                background-color: salmon;
                color: white; 
                border-radius: 10px;
            }
        }


    }

    .container {
        margin-top: 100px;
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: nowrap;

        .item {
            width: 220px;
            height: 150px;
            border-radius: 20px;
            bottom: 1px solid red;
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            overflow: hidden;
            position: relative;

            h3 {
                position: absolute;
                font-size: 20px;
                display: none;
                
                a {
                    text-decoration: none;
                    color: white;
                }
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;
            }

            &:hover {
                background-color: salmon;
                
                h3 {
                    display: block;
                    &:hover{
                        a {
                            color: black;
                        }
                        
                    }

                }

                img {
                opacity: 0.2;
                z-index: 0;
                }
            }
        }

    }
    
    .transition {
        margin-top: 100px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: nowrap;
        
        a{
            img{ 
                width: 50px;
                animation: arrowBlink 2s infinite;
                margin-right: 20px;
            }
    
            @keyframes arrowBlink {
                100%{
                    opacity: 0;
                }
            }
        }
    }
}